@import 'mixins';
@import 'variables';

.compare {
  color: $colorWhite;

  @media (min-width: 320px) {
    &__head {
      display: grid;
      grid-template-columns: repeat(1, 1fr);
    }

    &__h2 {
      font-weight: 500;
      font-size: 32px;
      line-height: 32px;
      letter-spacing: -1.5px;
      white-space: nowrap;
      text-align: center;
      padding-top: 92px;
    }

    &__h3 {
      font-weight: 700;
      font-size: 24px;
      line-height: 33.6px;
      padding-top: 32px;

      &-txt {
        font-weight: 400;
        padding-top: 8px;
        font-size: 16px;
        line-height: 22.4px;
      }
    }

    &__photo-bike1 {
      max-width: 280px;
      padding-top: 48px;
      padding-right: 20px;

      display: flex;
    }

    &__img {
      display: block;
      margin: 0 auto;
      max-width: 280px;
      padding-top: 40px;

      &:first-child {
        padding-top: 48px;
      }
    }

    &__costs {
      padding-top: 16px;
      font-weight: 700;
      line-height: 33.6px;
      font-size: 24px;
    }
  }

  @media (min-width: 576px) {
    &__head {
      display: grid;
      grid-template-columns: repeat(1, 6fr);
    }

    &__h2 {
      padding-top: 132px;
      font-weight: 500;
      font-size: 48px;
      line-height: 48px;
      letter-spacing: -1.5px;
    }

    &__img {
      max-width: 324px;
      display: flex;
      margin: 0 auto;
    }

    &__costs {
      padding-top: 16px;
      font-weight: 700;
      line-height: 33.6px;
      font-size: 24px;
    }

    &__h3 {
      font-weight: 700;
      font-size: 24px;
      line-height: 33.6px;
      padding-top: 32px;

      &-txt {
        font-weight: 400;
        padding-top: 8px;
        font-size: 16px;
        line-height: 22.4px;
      }
    }
  }

  @media (min-width: 1200px) {
    &__head {
      @include grid;
    }

    &__h2 {
      font-weight: 500;
      font-size: 64px;
      line-height: 64px;
      letter-spacing: -1.5px;
      text-align: center;
      padding-top: 164px;
    }

    &__img {
      transition: transform 0.3s ease;
      max-width: 100%;
      padding-top: 56px;
      margin-bottom: 32px;
      gap: 24px;
      display: flex;

      &:hover {
        transform: scale(1.1);
      }
    }

    &__position {
      grid-column: span 4;
    }

    &__costs {
      padding-top: 16px;
      font-weight: 700;
      line-height: 33.6px;
      font-size: 24px;
      gap: 24px;
    }

    &__h3 {
      font-weight: 700;
      font-size: 24px;
      line-height: 33.6px;
      padding-top: 32px;

      &-txt {
        font-weight: 400;
        padding-top: 8px;
        font-size: 16px;
        line-height: 22.4px;
      }
    }
  }
}
